/**
 * PetCareApp - Kontekst autoryzacji
 * Zarzdzanie stanem uytkownika, logowanie, wylogowanie, rejestracja
 * @author VS
 */

import React, { createContext, useContext, useState, useEffect, useCallback } from 'react';
import { authService } from '../services/authService';

// Tworzenie kontekstu - VS
const AuthContext = createContext(null);

/**
 * Provider kontekstu autoryzacji
 * @param {Object} props - Waciwoci komponentu
 * @param {React.ReactNode} props.children - Komponenty dzieci
 */
export function AuthProvider({ children }) {
    // Stan uytkownika - VS
    const [user, setUser] = useState(null);
    const [isLoading, setIsLoading] = useState(true);
    const [error, setError] = useState(null);

    // Sprawdzenie sesji przy starcie aplikacji - VS
    useEffect(() => {
        const checkAuth = async () => {
            try {
                const token = localStorage.getItem('accessToken');
                if (token) {
                    const userData = await authService.verifyToken(token);
                    setUser(userData);
                }
            } catch (err) {
                localStorage.removeItem('accessToken');
                localStorage.removeItem('refreshToken');
                console.error('Bd weryfikacji tokena:', err);
            } finally {
                setIsLoading(false);
            }
        };

        checkAuth();
    }, []);

    /**
     * Logowanie uytkownika
     * @param {string} email - Adres email
     * @param {string} password - Haso
     * @param {string} role - Rola uytkownika
     */
    const login = useCallback(async (email, password, role) => {
        setIsLoading(true);
        setError(null);

        try {
            const response = await authService.login(email, password, role);
            
            // Zapisanie tokenw - VS
            localStorage.setItem('accessToken', response.accessToken);
            localStorage.setItem('refreshToken', response.refreshToken);
            
            setUser(response.user);
            return response.user;
        } catch (err) {
            setError(err.message);
            throw err;
        } finally {
            setIsLoading(false);
        }
    }, []);

    /**
     * Rejestracja nowego uytkownika (tylko klient)
     * @param {Object} userData - Dane rejestracyjne
     */
    const register = useCallback(async (userData) => {
        setIsLoading(true);
        setError(null);

        try {
            const response = await authService.register(userData);
            return response;
        } catch (err) {
            setError(err.message);
            throw err;
        } finally {
            setIsLoading(false);
        }
    }, []);

    /**
     * Wylogowanie uytkownika
     */
    const logout = useCallback(async () => {
        try {
            await authService.logout();
        } catch (err) {
            console.error('Bd wylogowania:', err);
        } finally {
            // Zawsze czycimy stan lokalny - VS
            localStorage.removeItem('accessToken');
            localStorage.removeItem('refreshToken');
            setUser(null);
        }
    }, []);

    /**
     * Resetowanie hasa
     * @param {string} email - Adres email
     */
    const resetPassword = useCallback(async (email) => {
        setIsLoading(true);
        setError(null);

        try {
            await authService.resetPassword(email);
        } catch (err) {
            setError(err.message);
            throw err;
        } finally {
            setIsLoading(false);
        }
    }, []);

    /**
     * Aktualizacja danych uytkownika
     * @param {Object} updates - Dane do aktualizacji
     */
    const updateUser = useCallback(async (updates) => {
        try {
            const updatedUser = await authService.updateProfile(updates);
            setUser(prev => ({ ...prev, ...updatedUser }));
            return updatedUser;
        } catch (err) {
            setError(err.message);
            throw err;
        }
    }, []);

    // Warto kontekstu - VS
    const value = {
        user,
        isLoading,
        error,
        isAuthenticated: !!user,
        login,
        register,
        logout,
        resetPassword,
        updateUser,
        clearError: () => setError(null)
    };

    return (
        <AuthContext.Provider value={value}>
            {children}
        </AuthContext.Provider>
    );
}

/**
 * Hook do uycia kontekstu autoryzacji
 * @returns {Object} Kontekst autoryzacji
 */
export function useAuth() {
    const context = useContext(AuthContext);
    
    if (!context) {
        throw new Error('useAuth musi by uyty wewntrz AuthProvider');
    }
    
    return context;
}

export default AuthContext;
