/**
 * PetCareApp - Kontekst jzyka
 * Zarzdzanie przeczaniem jzykw PL/EN
 * @author VS
 */

import React, { createContext, useContext, useState, useCallback } from 'react';
import { useTranslation } from 'react-i18next';

// Dostpne jzyki - VS
export const LANGUAGES = {
    PL: 'pl',
    EN: 'en'
};

// Informacje o jzykach - VS
export const LANGUAGE_INFO = {
    pl: {
        code: 'pl',
        name: 'Polski',
        nativeName: 'Polski',
        flag: ''
    },
    en: {
        code: 'en',
        name: 'English',
        nativeName: 'English',
        flag: ''
    }
};

// Tworzenie kontekstu - VS
const LanguageContext = createContext(null);

/**
 * Provider kontekstu jzyka
 * @param {Object} props - Waciwoci komponentu
 * @param {React.ReactNode} props.children - Komponenty dzieci
 */
export function LanguageProvider({ children }) {
    const { i18n } = useTranslation();
    const [currentLanguage, setCurrentLanguage] = useState(i18n.language || LANGUAGES.PL);

    /**
     * Zmiana jzyka aplikacji
     * @param {string} lang - Kod jzyka (pl/en)
     */
    const changeLanguage = useCallback((lang) => {
        if (LANGUAGE_INFO[lang]) {
            i18n.changeLanguage(lang);
            setCurrentLanguage(lang);
            localStorage.setItem('language', lang);
            document.documentElement.lang = lang;
        }
    }, [i18n]);

    /**
     * Przeczenie na drugi dostpny jzyk
     */
    const toggleLanguage = useCallback(() => {
        const newLang = currentLanguage === LANGUAGES.PL ? LANGUAGES.EN : LANGUAGES.PL;
        changeLanguage(newLang);
    }, [currentLanguage, changeLanguage]);

    // Warto kontekstu - VS
    const value = {
        currentLanguage,
        languageInfo: LANGUAGE_INFO[currentLanguage],
        languages: LANGUAGE_INFO,
        changeLanguage,
        toggleLanguage,
        isPolish: currentLanguage === LANGUAGES.PL,
        isEnglish: currentLanguage === LANGUAGES.EN
    };

    return (
        <LanguageContext.Provider value={value}>
            {children}
        </LanguageContext.Provider>
    );
}

/**
 * Hook do uycia kontekstu jzyka
 * @returns {Object} Kontekst jzyka
 */
export function useLanguage() {
    const context = useContext(LanguageContext);
    
    if (!context) {
        throw new Error('useLanguage musi by uyty wewntrz LanguageProvider');
    }
    
    return context;
}

export default LanguageContext;
